}

.header {
  background-color: var(--brand-color);
  height: 60px;
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  padding-left: 40px;
  grid-area: header;

  display: flex;
  align-items: center;
}

.sidebar {
  grid-area: sidebar;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  padding-top: 5px;
  gap: 5px;
}
