bold;\n  color: white;\n  padding-left: 40px;\n  grid-area: header;\n\n  display: flex;\n  align-items: center;\n}\n\n.sidebar {\n  grid-area: sidebar;\n  display: flex;\n  flex-direction: column;\n  font-family: \"Poppins\", sans-serif;\n  font-weight: 400;\n  padding-top: 5px;\n  gap: 5px;\n}\n.sidebar > button {\n  height: 2.5rem;\n  border: none;\n  background-color: transparent;\n  padding: 0 20px;\n  margin: 0 5px;\n  display: grid;\n  grid-template-columns: min-content 1fr min-content;\n  align-items: center;\n  justify-items: start;\n  gap: 10px;\n  white-space: nowrap;\n}\n\n.sidebar > button:not(.active-list):hover {\n  cursor: pointer;\n  background-color: var(--backboard-color);\n  border-radius: 5px;\n}\n.Today::before {\n  content: \"\";\n  height: 25px;\n  width: 25px;\n  background-size: 25px 25px;\n  background-image: url(./images/sun-fog.svg);\n  background-repeat: no-repeat;\n}\n.Priority::before {\n  content: url(./images/star.svg);\n  width: 25px;\n  height: 25px;\n}\n.All-Tasks::before {\n  content: \"\";\n  height: 25px;\n  width: 25px;\n  background-size: 25px 25px;\n  background-image: url(./images/expand-all.svg);\n  background-repeat: no-repeat;\n}\n.add-new::before {\n  content: url(./images/plus.svg);\n  width: 25px;\n  height: 25px;\n}\n.custom-list::before {\n  content: url(./images/list-bullets.svg);\n  width: 25px;\n  height: 25px;\n}\n.sidebar > .active-list {\n  background-color: var(--active-color);\n  outline: 1px solid var(--active-border-color);\n  border-radius: 5px;\n}\n.divider {\n  height: 1px;\n  background-color: rgb(236, 236, 236);\n  margin: 6px 12px;\n}\n.sidebar > .add-new {\n  margin-bottom: 20px;\n  font-weight: 400;\n  background-color: var(--hover-color);\n  margin: 8px;\n  border-radius: 5px;\n  height: 3rem;\n}\n.backdrop {\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  background-color: rgba(0, 0, 0, 0.7);\n  z-index: 2;\n  display: none;\n\n  justify-content: center;\n  align-items: center;\n}\n.modal {\n  width: 35rem;\n  height: 20rem;\n  background-color: var(--color-white);\n  border-radius: 8px;\n  padding: 15px 15px;\n  margin: 0 10px;\n}\n.modal-form {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  height: 100%;\n}\n.modal-form > input {\n  height: 3rem;\n  padding: 0 10px;\n  border: none;\n  outline: 2px solid var(--backboard-color);\n  border-radius: 5px;\n}\n.modal-form > input:focus {\n  outline: 2px solid rgb(125, 125, 125);\n}\n.modal-form button {\n  align-self: flex-end;\n}\n.close-button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 2.5rem;\n  height: 2.5rem;\n  font-size: 1.5rem;\n  border: none;\n  background: none;\n  cursor: pointer;\n  color: var(--dark-gray);\n}\n.close-button:hover {\n  color: var(--active-border-color);\n}\n.continueButton {\n  padding: 10px 25px;\n  border-radius: 10px;\n  background-color: var(--brand-color);\n  cursor: pointer;\n  border-style: none;\n  color: white;\n}\n.continueButton:disabled {\n  background-color: rgb(221, 236, 226);\n  cursor: auto;\n}\n\n/* main-content area styling */\n.main {\n  grid-area: main;\n  background-color: rgb(236, 236, 236);\n  display: flex;\n  justify-content: start;\n  align-items: center;\n  gap: 30px;\n  padding: 30px 50px;\n  font-family: \"Poppins\", sans-serif;\n  font-weight: 300;\n  min-width: 200px;\n}\n.todo-container,\n.todo-details {\n  height: 95%;\n  border-radius: 8px;\n  background-color: var(--color-white);\n  flex: 1;\n}\n.todo-details {\n  display: none;\n  background-color: var(--color-white);\n}\n.add-todo-field {\n  border: 10px solid var(--hover-color);\n}\n.add-todo-field > form {\n  display: flex;\n}\n.add-todo-input {\n  flex-grow: 1;\n  height: 40px;\n  min-width: 2rem;\n  border: none;\n  padding-left: 10px;\n}\n.add-todo-input:focus {\n  border: none;\n  outline: none;\n}\n.add-todo-button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border: none;\n  background: none;\n  width: 40px;\n  height: 40px;\n  font-size: 40px;\n}\n.add-todo-button:hover {\n  cursor: pointer;\n  color: var(--active-border-color);\n}\n.todo-item {\n  display: flex;\n  align-items: center;\n  padding: 10px 10px;\n  height: 2.8rem;\n  gap: 15px;\n  cursor: pointer;\n  background-color: var(--hover-color);\n  margin: 10px 10px;\n  border-radius: 5px;\n  box-shadow: 0 5px 8px -2px rgb(225, 227, 224);\n}\n.todo-item:hover {\n  background-color: var(--backboard-color);\n}\n.todo-item > button {\n  cursor: pointer;\n  width: min-content;\n  border-radius: 50%;\n  height: 20px;\n  width: 20px;\n  border: solid;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.checkmark {\n  visibility: hidden;\n}\n.todo-item > .radio-button > .checkmark-visible {\n  visibility: visible;\n}\n.radio-button:hover .checkmark {\n  visibility: visible;\n}\n.completed-container {\n  display: none;\n}\n.completed-title {\n  margin: 25px 10px 0 10px;\n  padding: 5px 5px 0 5px;\n  border-top: 2px solid var(--hover-color);\n  color: gray;\n}\n.completed-todo {\n  color: gray;\n  text-decoration: line-through;\n}\n\n/* To-do details */\n.todo-details {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  padding: 10px 10px;\n}\n.todo-detail-header {\n  font-size: 2rem;\n}\n.todo-details > button {\n  cursor: pointer;\n  height: 5%;\n  border-radius: 8px;\n  border-style: none;\n  font-weight: 400;\n}\n.todo-details > button:hover {\n  background-color: var(--backboard-color);\n}\n.description {\n  height: 20%;\n  resize: none;\n  padding: 5px;\n  border-radius: 8px;\n  background-color: var(--hover-color);\n  border-style: none;\n}\n.description:focus {\n  outline: 1px solid rgb(213, 213, 213);\n}\n.priority,\n.due-date {\n  display: flex;\n  grid-template-columns: 25px 50%;\n  align-items: center;\n  justify-content: center;\n  gap: 10px;\n}\n.due-date-backdrop {\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  background-color: rgba(0, 0, 0, 0.7);\n  z-index: 2;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.due-date-modal {\n  background-color: white;\n  height: 15rem;\n  width: 30rem;\n  border-radius: 8px;\n  padding: 30px 15px 15px 15px;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  grid-template-rows: min-content min-content 1fr;\n  column-gap: 15px;\n  row-gap: 5px;\n  margin: 0 10px;\n}\n.due-date-modal > label {\n  font-weight: 400;\n  font-size: 1.2rem;\n}\n.due-date-modal > button {\n  height: 3rem;\n  align-self: end;\n  border-radius: 5px;\n  border-style: none;\n  cursor: pointer;\n  font-weight: 400;\n  color: white;\n}\n\n.setDueDate {\n  background-color: var(--brand-color);\n}\n.setDueDate:hover {\n  background-color: rgb(74, 168, 96);\n}\n.cancel-button {\n  background-color: rgb(221, 103, 103);\n}\n.cancel-button:hover {\n  background-color: rgb(174, 40, 40);\n}\n.remove {\n  background-color: rgb(221, 103, 103);\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.todo-details > .remove:hover {\n  background-color: rgb(174, 40, 40);\n}\n.remove > img {\n  filter: invert(100%);\n}\n\n.footer {\n  grid-area: footer;\n  background-color: rgb(220, 220, 220);\n  padding: 8px 0 8px 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n/* responsive design */\n@media (width<=1000px) {\n  .main {\n    flex-direction: column;\n    padding: 20px 20px;\n    gap: 10px;\n    align-items: stretch;\n  }\n  .todo-container,\n  .todo-details {\n    min-width: 300px;\n  }\n  .todo-details > button {\n    flex: 1;\n  }\n  .todo-details > .description {\n    flex: 1;\n  }\n}\n\n@media (width<=500px) {\n  body {\n    grid-template-columns: 1fr;\n    grid-template-rows: min-content 1fr 2fr min-content;\n    grid-template-areas:\n      \"header header\"\n      \"sidebar sidebar\"\n      \"main main\"\n      \"footer footer\";\n  }\n  button {\n    color: var(--button-color);\n  }\n  .radio-button:hover .checkmark {\n    visibility: hidden;\n  }\n  .todo-item > .radio-button > .checkmark-visible {\n    visibility: visible;\n  }\n  .main {\n    flex-direction: column;\n    padding: 20px 20px;\n    gap: 10px;\n    align-items: stretch;\n  }\n  .todo-container,\n  .todo-details {\n    width: 100%;\n  }\n  .sidebar {\n    margin-bottom: 10px;\n  }\n  .todo-details > button {\n    flex: 1;\n  }\n  .todo-details > .description {\n    flex: 1;\n  }\n}\n"],"sourceRoot":""}]);
